<template>
  <div>
    <van-sticky>
      <van-nav-bar title="绑定手机号" left-arrow @click-left="$router.push({path:'mall'})" />
    </van-sticky>
    <div class="home">
      <van-cell-group>
        <van-field v-model="phone" placeholder="请输入您的手机号码" clearable />
      </van-cell-group>
      <button @click="boundPhone" class="bindBtn">绑定</button>
    </div>
  </div>
</template>

<script>
import { bindPhone } from "@/api/mall";
export default {
  data() {
    return {
      phone: "",
      inviteCode: ""
    };
  },
  created(){
    if(this.$route.query.bindType==1||localStorage.getItem('bindType')==1){
       localStorage.setItem('bindType',1);
    }else {
      localStorage.setItem('bindType',2);
    }
  },
  methods: {
    // 绑定手机号
    boundPhone() {
      if (!this.phone) {
        this.$toast("请输入您的手机号码!");
        return false;
      } else {
        bindPhone(this.phone).then(res => {
          if(localStorage.getItem('bindType')==1){
            this.$router.push({path:"mall"});
          }else{
            this.$router.push({path:"scanOrder"});
          }
        });
      }
    }
  }
};
</script>
<style lang="less" scoped>
.home {
  margin-top: 30px;
}
/deep/ .van-field__control {
  border: 1px solid #999999;
  border-radius: 20px;
  padding-left: 20px;
  height: 40px;
  width: 80%;
  margin: 0 auto;
}

.bindBtn {
  background-color: #de4f53 !important;
  width: 250px;
  margin: 50px auto;
  border-radius: 20px;
  height: 40px;
  line-height: 40px;
  display: block;
  color: #fff;
  border: 0 none;
  font-size: 18px;
}
</style>
